import Link from 'next/link'

function getPosts () {
  return [
    { id: 'hello-nextjs', title: 'Hello Next.js'},
    { id: 'learn-nextjs', title: 'Learn Next.js is awesome'},
    { id: 'deploy-nextjs', title: 'Deploy apps with ZEIT'},
  ]
}

const linkStyle = {
  marginRight: 15
}

const contentM = {
  float: 'left',
  width: '260px',
  backgroundColor: 'red'
}

const Sidebar = () => (
    <div style={contentM}>
      <h1>Menu</h1>
      <ul>
        <li>
          <Link href="/">
            <a style={linkStyle}>Truc</a>
          </Link>
        </li>
        <li>
          <Link href="/">
            <a style={linkStyle}>Truc2</a>
          </Link>
        </li>
      </ul>
    </div>
)

export default Sidebar



